@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

*{
  background-color: rgb(37, 34, 34);
  color: white;
  font-family:Roboto,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
    text-decoration: none;
  scroll-behavior: smooth;
  user-select: none;
}

h2{
  text-align: center;
  font-size: 2em;
}

h3{
  text-align: center;
}

.elemento{
  display: inline;
  font-size: 2vw;
  text-align: center;
  padding-left: 5vh;
  padding-right: 5vh;
  width: 40%;
}

r{
  display: flex;
  padding-top: 10vh;
  justify-content: space-between;
}

footer{
  font-family: sans-serif;
  position: fixed;
  left: 0;
  bottom: 0;
  font-size: 1.5em;
  width: 100%;
  text-align: center;
  opacity: 0.8;
  background-color: rgb(20, 19, 19);

}




@media screen and (max-width: 800px){
  footer{
  font-size: 1.1em;
}


r{
  padding-top: 8vh;
}

.elemento{
  display: inline;
  font-size: 4.5vw;
  text-align: center;
  padding-left: 5vh;
  padding-right: 5vh;
  width: 40%;
}

  }